import React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import { wrap } from './Modal.module.scss';
import StorageIcon from '@mui/icons-material/Storage';
import ImportantDevicesIcon from '@mui/icons-material/ImportantDevices';
import BuildIcon from '@mui/icons-material/Build';
import PrimaryInfo from './reusable/PrimaryInfo';
import AboutSection from './reusable/AboutSection';
import Techstack from './reusable/Techstack';

const style = {
    bgcolor: 'background.paper',
};



const ProjectModal = ({ open, setOpen, title, frontendInfo, backendInfo, purpose, technologies, about, about2=null }) => {
    return (
        <div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                open={open}
                onClose={() => setOpen(false)}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                <Box sx={style} className={wrap}>
                    <h2>{title}</h2>
                    <table>
                        <tr>
                            <PrimaryInfo
                                title="Frontend" 
                                info={frontendInfo}
                                icon={<ImportantDevicesIcon/>}
                                gitLink="https://github.com"
                                demoLink="https://google.com"
                            />
                            <PrimaryInfo
                                title="Backend"
                                info={backendInfo}
                                icon={<StorageIcon/>}
                            />
                            <PrimaryInfo
                                title="Purpose"
                                info={purpose}
                                icon={<BuildIcon/>}
                            />

                        </tr>
                    </table>
                    <AboutSection about={about} about2={about2}/>
                    <Techstack techs={technologies}/>
                </Box>
                </Fade>
            </Modal>
        </div>
    );
};

export default ProjectModal;