import React, { useContext } from 'react';
import { wrap, cardWrap } from './EducationSection.module.scss';
import ExperienceTitle from './ExperienceSection/ExperienceTitle';
import sdsu from '../../images/education/sdsu-logo.png';
import gross from '../../images/education/logo-grossmont.png';
import SchoolCard from './EducationSection/SchoolCard';
import { RefsContext } from '../../pages/index';


const educations = [
    {
        imageSrc: sdsu,
        degree: "B.B.A., Management Information Systems"
    },
    {
        imageSrc: gross,
        degree: "A.S., Computer Programming"
    }
];

const EducationSection = () => {

    const context = useContext(RefsContext)
    return (
        <div ref={context.educationRef} className={wrap}>
            <ExperienceTitle title="EDUCATION"/>
            <div className={cardWrap}>
            {
                educations.map((e, i) => <SchoolCard key={i} {...e}/>)
            }
            </div>
        </div>
    );
};

export default EducationSection;